/******************************************************************

Stylesheet: WP Media

******************************************************************/

.size-auto,
.size-full,
.size-large,
.size-medium,
.size-thumbnail {
	max-width: 100%;
	height: auto;
}

@include breakpoint(small) {
	.alignleft, img.alignleft, figure.alignleft, dl.alignleft {
		display: inline;
		margin-left: 0em;
		margin-right: $default_margin;
	}
	.alignright, img.alignright, figure.alignright, dl.alignright {
		display: inline;
		margin-left: $default_margin;
		margin-right: 0em;
	}
	.aligncenter, img.aligncenter, figure.aligncenter, dl.aligncenter {
		clear: both;
		display: block;
		margin-right: auto;
		margin-left: auto;
	}
}

.alignnone, img.alignnone, figure.alignnone, dl.alignnone, .alignnone img  {
	margin-left: 0;
	margin-right: 0;
	// width: 100% !important;
	height: auto;
}


/*********************
LAZYLOADING IMAGES - uses lazysizes script
*********************/

IMG[src=''], IMG:not([src]) {
	opacity: 0; // hides border for images without src
}

// figure classes
.lazyhd,
.lazysquare,
.lazy70 {
	position: relative;
	height: 0;
	background: $gray_light;
	img {
		width: 100%;
		height: auto;
		opacity: 0.2;
		transition: opacity $easeslow;
	}
}
.lazyhd {
	padding-bottom: 56.25%;
}
.lazysquare {
	padding-bottom: 100%;
}
.lazy70 {
	padding-bottom: 70%;
}

// Lazysizes defaults
.lazyload {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
}
.lazyloading {
	opacity: 0.2;
}
.lazyloaded {
	opacity: 1 !important;
}


/*********************
WP CAPTIONS
*********************/

.wp-caption {
	max-width: 100%;
	text-align: center;
	&.alignleft {
		margin-right: $default_margin;
	}
	&.alignright {
		margin-left: $default_margin;
	}
	&.aligncenter {
		margin-right: auto;
		margin-left: auto;
	}

	/* images inside wp-caption */
	img {
		max-width: 100%;
		height: auto;
		margin-bottom: 0;
	}

	.wp-caption-text {
		font-size: 0.875em;
		margin: .5em 0 1em;
		text-align: center;
	}
}


/*********************
HTML5 IMAGE GALLERY
*********************/

.gallery {
	text-align: justify;
	.gallery-item {
		margin: 0;
		width: 50%;
		display: inline-block;
		vertical-align: top;
		padding: .5em;
		// outline: none;
	}
	img {
		display: block;
	}
	.gallery-caption {
		text-align: left;
		padding: .25em 0;
	}
	&::after {
	    content: '';
	    display: inline-block;
	    width: 100%;
	}
@include breakpoint(medium) {
	.gallery-item {
		width: 25%;
	}
	img {
		will-change: opacity;
		&:hover {
			opacity: .7;
			transition: opacity $ease;
		}
	}
}
}


/*********************
RESPONSIVE VIDEOS
*********************/

// General Video Styles
.videoContainer {
	position: relative; 
	padding-bottom: 56.25%; 
	height: 0; 
	overflow: hidden;
}

.videoContainer iframe, 
.videoContainer object, 
.videoContainer embed, 
.videoContainer video { 
	position: absolute; 
	top: 0; 
	left: 0; 
	width: 100%;
	height: 100%; 
}
